const rgbToHex = (rgb) => {
  let hexTemp = []
  let hex     = "#"
      rgb     = rgb.match(/^rgb\((\d+),\s*(\d+),\s*(\d+)\)$/)

  for (let i = 0; i<= 2; i++) {
    hexTemp[i] = Number(rgb[i+1]).toString(16)

    if (hexTemp[i].length < 2)
      hexTemp[i] = '0' + hexTemp[i]

    hex += hexTemp[i]
  }

  return hex
}
