const setColors = ($colors)  => {
  $colors.forEach(($color) => {
    const styles = getComputedStyle($color, null)
    const $colorContainer = $color.closest('.color-container')

    $colorContainer.querySelector('[color-hex]').innerHTML = rgbToHex(styles.backgroundColor)
    $colorContainer.querySelector('[color-rgb]').innerHTML = styles.backgroundColor
  })
}

const Colors = () => {
  const $colors = document.querySelectorAll('[color]')

  if (!$colors.length > 0) return

  setColors($colors)
}

export default Colors
